@import '../variables';

#portfolio {
  > #block1 {
    background: $color-cararra;

    > .centered {
      padding: 260px 0 135px;

      .text-grid {
        margin-bottom: 65px;

        .icon-1 {
          background: $url-18 no-repeat 0 0;
          height: 21px;
          position: absolute;
          right: -9.09%;
          top: -150px;
          width: 29px;
        }
      }

      .framed-block {
        margin-left: -4.54%;
        width: 109.09%;

        .content {
          overflow: hidden;
          position: relative;

          > div {
            float: left;
            position: relative;
            width: 50%;

            .intro {
              color: transparent;
              padding: 80px 5.9% 110px;
              position: relative;
              transition-delay: 1s;
              transition-duration: 0.45s;
              transition-property: color;
              transition-timing-function: 'ease-in-out';

              h2 {
                font-family: $font-0, $font-1;
                font-size: 14px;
                letter-spacing: 0.65em;
                padding-bottom: 35px;
                text-transform: uppercase;
              }

              p {
                font-family: $font-2;
                font-size: 14px;
                line-height: 28px;
              }

              .border {
                display: none;
              }
            }

            .separator {
              background: transparent;
              height: 21px;
              padding-right: 2px;
              position: relative;
              transition-delay: 0.5s;
              transition-duration: 0.45s;
              transition-property: background;
              transition-timing-function: 'ease-in-out';
              width: 200%;
              z-index: 10;

              .border {
                > div {
                  transition-delay: 0.5s;
                }
              }
            }

            ul {
              filter: alpha(opacity=0);
              opacity: 0;
              padding: 60px 5.9% 85px;
              position: relative;
              // transition-delay: 0s;
              transition-delay: 1.3s;
              transition-duration: 0.45s;
              transition-property: opacity;
              transition-timing-function: 'ease-in-out';

              a {
                background: $color-rangoon-green;
                color: $color-rangoon-green;
                display: block;
                position: relative;

                > img {
                  transition-delay: 0s;
                  transition-duration: 0.75s;
                  transition-property: opacity;
                  transition-timing-function: 'ease-in-out';
                  width: 100%;

                  &:hover {
                    filter: alpha(opacity=70);
                    opacity: 0.7;
                  }
                }

                > .card-container {
                  bottom: -20px;
                  position: absolute;
                  right: -20px;

                  .content {
                    overflow: hidden;

                    h2 {
                      @extend %extend-3;
                      float: left;
                      font-family: $font-0, $font-1;
                      font-size: 15px;
                      letter-spacing: 0.65em;
                      line-height: 50px;
                      padding: 18px 30px 0;
                      position: relative;
                      text-transform: uppercase;
                      white-space: nowrap;
                    }

                    h3 {
                      float: right;
                      position: relative;

                      > span {
                        @extend %extend-3;
                        transition-delay: 1.35s;

                        .effect {
                          height: 79px;

                          &::before {
                            font-size: 30px;
                            line-height: 79px;
                          }

                          .small {
                            display: block;
                            font-family: $font-0, $font-1;
                            font-size: 8px;
                            left: 10px;
                            letter-spacing: 0.1em;
                            position: absolute;
                            text-transform: uppercase;
                            top: 7px;
                          }

                          .big {
                            display: block;
                            font-family: $font-2;
                            font-size: 30px;
                            line-height: 50px;
                            padding: 15px 25px 14px;
                          }
                        }
                      }

                      .border {
                        > div {
                          transition-delay: 1.1s;
                        }
                      }
                    }
                  }

                  &.loaded {
                    .content {
                      > h2,
                      > h3 > span {
                        filter: alpha(opacity=100);
                        opacity: 1;
                      }
                    }
                  }
                }
              }

              > li + li {
                padding-top: 60px;
              }
            }

            &.left-block {
              > div {
                padding-right: 1px;
                position: relative;

                > .border {
                  > div {
                    transition-delay: 0.75s;
                  }
                }
              }
            }

            &.right-block {
              > div {
                position: relative;

                .intro {
                  transition-delay: 1.15s;
                }

                ul {
                  margin-top: 21px;
                  transition-delay: 1.45s;
                }
              }
            }
          }

          .icon-door {
            background: $url-19 no-repeat 0 0;
            bottom: 0;
            height: 32px;
            left: 2.95%;
            margin-left: 1px;
            position: absolute;
            width: 40px;
            z-index: 10;
          }
        }

        &.loaded {
          .content {
            > div {
              .intro {
                color: $color-rangoon-green;
              }

              .separator {
                background: $color-cararra;

                .border > div {
                  height: 100%;
                  width: 100%;
                }
              }

              ul {
                filter: alpha(opacity=100);
                opacity: 1;
              }

              > div {
                > .border {
                  > div {
                    height: 100%;
                    width: 100%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
