@import '../variables';

.card-container {
  position: relative;
  z-index: 10;

  > div {
    background: $white;
    overflow: hidden;
    position: relative;
    transition-delay: 0s;
    transition-duration: 0.75s;
    transition-property: width;
    transition-timing-function: 'ease-in-out';
    width: 0;

    > div {
      margin: 20px;
      padding: 4px;
      position: relative;

      .frame {
        position: absolute;

        > div {
          background: $color-rangoon-green;
          overflow: hidden;
          position: absolute;
          transition-delay: 0.75s;
          transition-duration: 0.75s;
          transition-property: width, height;
          transition-timing-function: 'ease-in-out';
        }

        &.top {
          height: 4px;
          left: 0;
          top: 0;
          width: 100%;

          > div {
            height: 100%;
            left: 0;
            top: 0;
            width: 0;
          }
        }

        &.right {
          height: 100%;
          right: 0;
          top: 0;
          width: 4px;

          > div {
            height: 0;
            left: 0;
            top: 0;
            width: 100%;
          }
        }

        &.bottom {
          bottom: 0;
          height: 4px;
          right: 0;
          width: 100%;

          > div {
            height: 100%;
            right: 0;
            top: 0;
            width: 0;
          }
        }

        &.left {
          bottom: 0;
          height: 100%;
          left: 0;
          width: 4px;

          > div {
            bottom: 0;
            height: 0;
            left: 0;
            width: 100%;
          }
        }
      }

      .border {
        position: absolute;
        z-index: 15;

        > div {
          background: $color-chicago;
          overflow: hidden;
          position: absolute;
          transition-delay: 1s;
          transition-duration: 0.75s;
          transition-property: width, height;
          transition-timing-function: 'ease-in-out';
        }

        &.top {
          height: 2px;
          left: 0;
          top: 0;
          width: 100%;

          > div {
            height: 100%;
            left: 0;
            top: 0;
            width: 0;
          }
        }

        &.right {
          height: 100%;
          right: 0;
          top: 0;
          width: 2px;

          > div {
            height: 0;
            left: 0;
            top: 0;
            width: 100%;
          }
        }

        &.bottom {
          bottom: 0;
          height: 2px;
          right: 0;
          width: 100%;

          > div {
            height: 100%;
            right: 0;
            top: 0;
            width: 0;
          }
        }

        &.left {
          bottom: 0;
          height: 100%;
          left: 0;
          width: 2px;

          > div {
            bottom: 0;
            height: 0;
            left: 0;
            width: 100%;
          }
        }
      }

      .content {
        position: relative;
      }
    }
  }

  &.home {
    cursor: default;
    left: 50%;
    margin-left: -278px;
    margin-top: -168px;
    position: absolute;
    top: 50%;
    width: 557px;
    z-index: 50;

    h2 {
      position: relative;
      width: 100%;

      .logo {
        background: $url-4 no-repeat 0 0;
        display: block;
        height: 118px;
        text-indent: -5000px;
      }

      .btn-lang {
        border-bottom: 1px solid transparent;
        color: $color-rangoon-green;
        display: block;
        font-family: $font-0, $font-1;
        font-size: 13px;
        left: 40px;
        margin: 13px 20px;
        position: absolute;
        text-transform: uppercase;
        top: 0;

        &:hover {
          border-color: $color-rangoon-green;
        }
      }
    }

    h3 {
      background: $url-5 no-repeat 261px 12px;
      color: transparent;
      overflow: hidden;
      padding-bottom: 1px;
      position: relative;
      transition-delay: 1.35s;
      transition-duration: 0.45s;
      transition-property: color;
      transition-timing-function: 'ease-in-out';

      > span {
        display: block;
        font-family: $font-0, $font-1;
        font-size: 14px;
        letter-spacing: 0.65em;
        line-height: 40px;
        text-transform: uppercase;

        &.left {
          float: left;
          padding-left: 35px;
        }

        &.right {
          float: right;
          padding-right: 35px;
        }
      }

      .border > div {
        transition-delay: 1.1s;
      }
    }

    .menu {
      margin: 0 59px;
      overflow: hidden;

      > li {
        float: left;
        position: relative;

        > a {
          color: $color-rangoon-green;
          display: block;
          filter: alpha(opacity=0);
          height: 65px;
          opacity: 0;
          overflow: hidden;
          position: relative;
          transition-delay: 1.45s;
          transition-duration: 0.45s;
          transition-property: opacity;
          transition-timing-function: 'ease-in-out';
          z-index: 1;

          > span {
            &::before {
              padding: 0;
              text-align: center;
            }

            .small {
              display: block;
              font-family: $font-0, $font-1;
              font-size: 8px;
              left: 10px;
              letter-spacing: 0.1em;
              position: absolute;
              text-transform: uppercase;
              top: 7px;
            }

            .big {
              display: block;
              font-size: 15px;
              line-height: 50px;
              position: relative;
              text-align: center;
              transition-delay: 0s;
              transition-duration: 0.45s;
              transition-property: margin-top, background;
              transition-timing-function: 'ease-in-out';

              > span {
                display: block;
                font-family: $font-2;
                font-size: 17px;
                padding: 15px 0 0;
                white-space: nowrap;
              }
            }
          }
        }

        &:nth-child(1) {
          width: 33%;
        }

        &:nth-child(2) {
          width: 22%;

          > a {
            transition-delay: 1.55s;
          }

          .border > div {
            transition-delay: 1.3s;
          }
        }

        &:nth-child(3) {
          width: 45%;

          > a {
            transition-delay: 1.65s;
          }

          .border > div {
            transition-delay: 1.4s;
          }
        }

        &:nth-child(4) {
          width: 50%;

          > a {
            transition-delay: 1.75s;
          }

          .border > div {
            transition-delay: 1.5s;
          }
        }

        &:nth-child(5) {
          width: 50%;

          > a {
            transition-delay: 1.85s;
          }

          .border > div {
            transition-delay: 1.6s;
          }
        }

        .border > div {
          transition-delay: 1.2s;
        }
      }
    }

    &.loaded {
      .menu > li > a {
        filter: alpha(opacity=100);
        opacity: 1;
      }

      .btn-previous {
        .small {
          color: $color-rangoon-green;
        }
      }

      .btn-next {
        .small {
          color: $color-rangoon-green;
        }
      }
    }

    .btn-previous {
      background: $url-1 repeat-x center 54px;
      bottom: 0;
      display: block;
      height: 130px;
      left: 0;
      position: absolute;
      width: 59px;

      .small {
        color: transparent;
        display: block;
        font-family: $font-0, $font-1;
        font-size: 8px;
        left: 10px;
        letter-spacing: 0.1em;
        position: absolute;
        text-transform: uppercase;
        top: 7px;
        transition-delay: 1.25s;
        transition-duration: 0.45s;
        transition-property: color;
        transition-timing-function: 'ease-in-out';
      }

      &:hover {
        background-position: -58px 54px;
      }
    }

    .btn-next {
      background: $url-2 repeat-x center 54px;
      bottom: 0;
      display: block;
      height: 130px;
      position: absolute;
      right: 0;
      width: 59px;

      .small {
        color: transparent;
        display: block;
        font-family: $font-0, $font-1;
        font-size: 8px;
        left: 10px;
        letter-spacing: 0.1em;
        position: absolute;
        text-transform: uppercase;
        top: 7px;
        transition-delay: 1.25s;
        transition-duration: 0.45s;
        transition-property: color;
        transition-timing-function: 'ease-in-out';
      }

      &:hover {
        background-position: 58px 54px;
      }
    }

    > div {
      > div {
        height: 289px;
        width: 509px;
      }
    }
  }

  &.loaded {
    > div {
      width: 100%;

      > div {
        .frame,
        .border {
          > div {
            height: 100%;
            width: 100%;
          }
        }

        .content > * {
          color: $color-rangoon-green;
        }
      }
    }
  }
}
