@import '../variables';

#contact {
  > #block1 {
    background: $color-mongoose;

    > .centered {
      padding: 260px 0 135px;

      .share-block {
        margin-right: -25px;
        position: absolute;
        right: 9.09%;
        top: 0;
        width: 40.9%;

        @media (max-width: 1380px) {
          right: 0;
          width: 50%;
        }

        h2 {
          font-family: $font-0, $font-1;
          padding: 60px 0 55px;
          position: relative;

          .small {
            background: $url-26 no-repeat 0 0;
            display: block;
            font-size: 8px;
            left: -25px;
            letter-spacing: 0.1em;
            padding: 24px 0 0 10px;
            position: absolute;
            text-transform: uppercase;
            top: 0;
          }

          .big {
            display: block;
            font-family: $font-0, $font-1;
            font-size: 15px;
            letter-spacing: 0.65em;
            text-transform: uppercase;
          }
        }

        p {
          font-family: $font-2;
          font-size: 14px;
          line-height: 28px;
          max-width: 380px;
        }

        ul {
          background: $url-26 no-repeat right 0;
          left: 220px;
          padding-right: 2px;
          position: absolute;
          top: 0;

          @media (max-width: 1380px) {
            left: 189px;
          }

          > li {
            background: $url-26 no-repeat 0 0;
            float: left;

            a {
              background: $url-27 no-repeat right bottom;
              display: block;
              height: 72px;
              width: 88px;

              span {
                border-bottom: 1px solid transparent;
                color: $color-rangoon-green;
                display: inline-block;
                font-family: $font-0, $font-1;
                font-size: 8px;
                letter-spacing: 0.1em;
                margin: 24px 0 0 10px;
                text-transform: uppercase;
              }

              &:hover {
                span {
                  border-color: $color-rangoon-green;
                }
              }
            }

            &.twitter {
              a {
                background-image: $url-28;
              }
            }

            &.pinterest {
              a {
                background-image: $url-29;
              }
            }
          }
        }
      }

      .text-grid {
        margin-bottom: 65px;

        .icon-1 {
          background: $url-30 no-repeat 0 0;
          height: 36px;
          position: absolute;
          right: -10px;
          top: -75px;
          width: 16px;
        }
      }

      .framed-block {
        margin-left: -4.54%;
        width: 109.09%;

        .content {
          overflow: hidden;

          > div {
            float: left;
            width: 50%;

            .map {
              @extend %extend-4;
              position: relative;

              > img {
                height: auto;
                width: 100%;
              }

              > div {
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
                z-index: 1;

                .gmnoprint {
                  display: none !important;
                }

                a {
                  display: none !important;
                }
              }
            }

            .card-container {
              @extend %extend-4;
              margin-left: 7.9%;
              margin-top: -80px;
              width: 478px;

              @media (max-width: 1380px) {
                margin-left: 0;
                width: 413px !important;
              }

              .content {
                > * {
                  color: transparent;
                  transition-delay: 1.25s;
                  transition-duration: 0.45s;
                  transition-property: color;
                  transition-timing-function: 'ease-in-out';
                }

                h2 {
                  float: left;
                  font-family: $font-0, $font-1;
                  padding: 0 25px;
                  position: relative;

                  .small {
                    display: block;
                    font-size: 8px;
                    left: 10px;
                    letter-spacing: 0.1em;
                    position: absolute;
                    text-transform: uppercase;
                    top: 7px;
                  }

                  .big {
                    display: block;
                    font-size: 16px;
                    letter-spacing: 0.65em;
                    line-height: 50px;
                    padding-top: 15px;
                    text-transform: uppercase;

                    @media (max-width: 1380px) {
                      font-size: 14px;
                      letter-spacing: 0.4em;
                    }
                  }
                }

                h3 {
                  float: left;
                  padding: 0 25px;
                  position: relative;
                  transition-delay: 1.35s;

                  .small {
                    display: block;
                    font-family: $font-0, $font-1;
                    font-size: 8px;
                    left: 10px;
                    letter-spacing: 0.1em;
                    position: absolute;
                    text-transform: uppercase;
                    top: 7px;
                  }

                  .big {
                    display: block;
                    font-family: $font-2;
                    font-size: 17px;
                    line-height: 50px;
                    padding-top: 15px;
                    text-transform: uppercase;

                    @media (max-width: 1380px) {
                      font-size: 15px;
                    }
                  }

                  .border > div {
                    transition-delay: 1.1s;
                  }
                }
              }

              > div {
                > div {
                  width: 430px;

                  @media (max-width: 1380px) {
                    width: 365px !important;
                  }
                }
              }

              &.loaded {
                .content > * {
                  color: $color-rangoon-green;
                }
              }
            }

            ul {
              @extend %extend-4;
              padding: 85px 80px 70px;

              li {
                font-family: $font-2;
                position: relative;

                h3 {
                  font-size: 20px;
                  padding-bottom: 6px;
                }

                p {
                  font-size: 16px;
                  line-height: 30px;

                  a {
                    border-bottom: 1px solid $color-rangoon-green;
                    color: $color-rangoon-green;

                    &:hover {
                      border-color: transparent;
                    }
                  }
                }

                + li {
                  margin-top: 45px;
                }

                > .nb {
                  color: $white;
                  display: block;
                  font-family: $font-0, $font-1;
                  font-size: 8px;
                  left: -41px;
                  letter-spacing: 0.1em;
                  position: absolute;
                  top: 5px;
                }
              }
            }

            .sectors {
              color: transparent;
              font-family: $font-2;
              padding: 60px 38px 45px;
              position: relative;
              transition-delay: 0.25s;
              transition-duration: 0.45s;
              transition-property: color;
              transition-timing-function: 'ease-in-out';

              h3 {
                font-size: 16px;
                font-weight: 700;
                padding-bottom: 10px;
              }

              p {
                font-size: 14px;
                line-height: 28px;
              }

              &.loaded {
                color: $white;
              }
            }

            > div > .border > div {
              transition-delay: 0.5s;
            }

            &.left-block {
              > div {
                margin-right: 10px;
                padding-right: 1px;
                position: relative;

                .card-container {
                  width: 423px;

                  > div > div {
                    width: 375px;
                  }
                }

                .border {
                  &.bottom {
                    display: none;
                    height: 4px;
                  }
                }
              }
            }

            &.right-block {
              > div {
                margin-left: 10px;
                padding-left: 1px;
                position: relative;

                .map {
                  transition-delay: 1s;
                }

                .card-container {
                  transition-delay: 1s;
                  width: 423px;

                  > div > div {
                    width: 375px;
                  }
                }

                ul {
                  transition-delay: 1s;
                }

                .sectors {
                  transition-delay: 0.35s;

                  .border > div {
                    transition-delay: 0.1s;
                  }
                }
              }
            }
          }
        }

        &.loaded > .content {
          .map,
          .card-container,
          ul {
            filter: alpha(opacity=100);
            opacity: 1;
          }

          > div {
            > div {
              > .border {
                > div {
                  height: 100%;
                  width: 100%;
                }
              }
            }
          }
        }
      }

      > {
        .icon-1 {
          background: $url-30 no-repeat 0 0;
          height: 36px;
          left: 27.27%;
          margin-left: -4px;
          position: absolute;
          top: 1000px;
          width: 16px;
        }

        .icon-2 {
          background: $url-30 no-repeat 0 0;
          height: 36px;
          margin-right: -10px;
          position: absolute;
          right: 9.09%;
          top: 0;
          width: 16px;
        }
      }
    }
  }
}
